.moon {
	  width: 100px; 
  	height: 100px; 
  	border-radius: 50%; 
  	box-shadow: 15px 15px 0 0 black; 
	  margin: 0 auto;
	  margin-top: 10%;
} 

.title {
	background-color: transparent;
	padding: 10px;
	max-width: 60%;
	margin: auto;
	margin-top: 5%;
	text-align: center;
	position: relative;
	height: 200px;
	white-space: nowrap;
}

/* BORDAS TITULO */
.title:before {
  	content : "";
  	position: absolute;
  	left    : 50%;
  	bottom  : 30%;
  	height  : 1px;
 	  width   : 3%;  /* or 100px */
	  border-left: 10px solid transparent; 
  	border-right: 10px solid transparent; 
  	border-top: 15px solid rgba(191,0,0,0.7);
}

.title:after {
  	content : "";
  	position: absolute;
  	left    : 44%;
  	bottom  : 80%;
  	height  : 1px;
 	  width   : 20%;  /* or 100px */
	  border-bottom: 10px solid rgba(191,0,0,0.7); 
  	border-left: 3px solid transparent; 
  	border-right: 3px solid transparent;
}

#logo {  
  color: #555;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 800;
  letter-spacing: -3px;
  line-height: 1;
  text-shadow: #EDEDED 3px 2px 0;
  font-size: 4em;
}

.wrapper-logo{
  -webkit-justify-content: center;
          justify-content: center;
  margin: auto;
  min-width: 400px;
}

@media only screen and (max-width: 768px) {
  .moon{ 
    margin-bottom: 50px; 
    margin-top: 35%;
  }
  .title:after{
    left: 45%;
  }
}

@-webkit-keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}


/*CONTAINER BOX*/
.container-warning{
  width: 230px;
  margin-left: 30%;
  height: 400px;
  top: 100px;
  right: 50px;
  position: fixed;
  z-index: 1;
  -webkit-animation: fadeInLeft 3s linear;
          animation: fadeInLeft 3s linear;
}

/*THOUGHT BOX */
.thought{
   	background-color: rgba(244, 160, 83,1);
    border-radius: 15px;
    width: 200px;
    min-height: 100px;
    float: right;
    padding: 10px;
    font-style: italic;
    position: relative;
}

.thought p {
  font-size: 1.3em;
  text-align: center;
  margin-top: 15px;
}

.thought:after{
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-top: 20px solid rgba(244, 160, 83,1);
    bottom: -20px;
    left: 20%;
}

/* GHOST */
.field {
  margin: 5% auto;
  display: table;
}

[class*="ghost"] {
  margin: 25px;
  height: 200px;
  width: 200px;
  position: relative;
}
[class*="ghost"] .head {
  border-top-left-radius: 100px;
  border-top-right-radius: 100px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  height: 150px;
  width: 152px;
  background: rgba(213, 74, 74, 1);
  position: relative;
}
[class*="ghost"] .head .eye,
[class*="ghost"] .head .eyeBack {
  border-top-left-radius: 100%;
  border-top-right-radius: 100%;
  border-bottom-left-radius: 100%;
  border-bottom-right-radius: 100%;
  position: relative;
}
[class*="ghost"] .head .eyeBack {
  height: 50px;
  width: 50px;
  background: white;
}
[class*="ghost"] .head .eyeBack:first-child {
  margin: 50px 0 0 25px;
  float: left;
}
[class*="ghost"] .head .eyeBack:last-child {
  margin: 50px 25px 0 0;
  float: right;
}
[class*="ghost"] .head .eye {
  margin: 20% auto;
  height: 25px;
  width: 25px;
  background: black;
}
[class*="ghost"] .legs {
  height: 40px;
  width: 0;
  border-right: 38px solid rgba(213, 74, 74, 1);
  border-left: 38px solid rgba(213, 74, 74, 1);
  border-bottom: 40px solid transparent;
  display: block;
  float: left;
}

/*CLOSE BUTTON*/

[class*='close-'] {
  color: #777;
  font: 22px/100% arial, sans-serif;
  position: absolute;
  right: 3px;
  text-decoration: none;
  text-shadow: 0 1px 0 #fff;
  top: 3px;
  cursor: pointer
}

.close-thik:after {
  content: '\2716'; 
}

